.filter{
	padding: .3rem .18rem;
	display: -ms-box;
	display: -webkit-box;
	display: box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	background: #fff;

	li{
		margin-right: .25rem;
		font-size: .22rem;
		background: #ececec;
		color: #696969;
		line-height: .33rem;
		height: .33rem;
		text-align: center;
		border-radius: .15rem;
		-ms-box-flex: 1;
		-webkit-box-flex: 1;
		box-flex: 1;
		-ms-flex: 1;
		-webkit-flex: 1;
		flex: 1;

		&:last-child{
			margin-right: 0;
		}

		&.active{
			background: #dc0129;
			color: #fff;
		}
	}
}