.top-item{
	margin-bottom: .05rem;
	height: 2.1rem;
	background-color: #fff;
	padding-left: 2.36rem;
	position: relative;
	padding-right: .18rem;

	.left-img{
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 2.1rem;
	}
	.left-img img{
		width: 100%;
		height: 100%;
	}
	.right-con{
		position: relative;
		height: 100%;
		padding-top: .55rem;
		-webkit-box-sizing:border-box;
	    box-sizing:border-box;
	}
	.act-title{
		font-size: 0;
		line-height: 1;
		position: relative;
		height: .33rem;
		margin-bottom: .22rem;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
	}
	.act-title span{
		display: inline-block;
	}
	.act-name{
		font-size: .27rem;
		line-height: 1;
		position: absolute;
		left: 0;
		bottom: 0;
		color: #000;
	}
	.arrow{
		position: absolute;
		right: 0;
		top: 50%;
		-webkit-transform: translateY(-50%);
		transform: translateY(-50%);
	}
	.act-info{
		font-size: .22rem;
		line-height: 1;
		position: absolute;
		right: 0;
		bottom: 0;
		white-space: nowrap;
		padding-right: 0.18rem;
		-webkit-box-sizing:border-box;
	    box-sizing:border-box;
		/*padding-right: .23rem;*/
		color: #000;
	}
	.detail{
		font-size: .24rem;
		color: #6d6d6d;
	    overflow: hidden;
	}
	.price-info{
		position: absolute;
		width: 100%;
		bottom: .1rem;
		right: 0;
		line-height: 1;
	}
	.o-price{
		font-size: .23rem;
		padding-bottom: .22rem;
	}
	.o-price del{
		color: #3b3b3b;
	}
	.out-count{
		float: right;
		color: #000;
		font-size: .21rem;
	}
	.out-count span{
		color: #dc0129;
		display: inline-block;
		padding: 0 .07rem;
	}
	.n-price{
		height: .35rem;
		position: relative;
	}
	.n-price p{
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		line-height: 1;
		font-size: 0;
		padding-top: .04rem;
	}
	.n-price p span{
		display: inline-block;
		vertical-align: middle;
	}
	.num-txt{
		font-size: .35rem;
		color: #dc0129;
		padding-right: .42rem;
	}
	.discount{
		display: inline-block;
		font-size: .22rem;
		color: #fff;
		background-color: #dc0129;
		padding: 0rem .08rem;
		text-align: center;
		line-height: .33rem;
		border-radius: .04rem;
	}
	.discount i{
		background: red;
		position: absolute;
		top:0;
		white-space: nowrap;
		height: 0;
		left: 50%;
		top: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
		vertical-align: top;
		font-family: Arial;
	}
	.buy-btn{
		width: 1.2rem;
		height: .3rem;
		position: absolute;
		right: 0;
		bottom: 0;
	}
	.buy-btn-inner{
		width: 2.4rem;
		line-height: .6rem;
		font-size: .42rem;
		text-align: center;
		-webkit-transform: scale(.5,.5);
		transform: scale(.5,.5);
		-webkit-transform-origin: 0 0;
		transform-origin: 0 0;
		border: 1px solid #000;
		color: #000;
	}
	.dry-up:after{
		position: absolute;
	    width: 1rem;
	    height: 1rem;
	    text-align: center;
	    line-height: 1rem;
	    font-size: .24rem;
	    background: rgba(0,0,0,.6);
	    color: #fff;
	    left: 50%;
	    top: 50%;
	    -webkit-transform: translate(-50%,-50%);
	    transform: translate(-50%,-50%);
	    content: '已抢光';
	    border-radius: 50%;
	}
	.top-tag{
		display: block;
		width: .44rem;
		height: .37rem;
		position: absolute;
		right: .06rem;
		top: .06rem;
		background: url(../images/top_tag.png) no-repeat;
		background-size: contain;
		color: #fff;
		font-size: .15rem;
		text-align: center;
		line-height: .58rem;
	}
}